#modal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: scroll;
    inset: 0px;
    color: #111;
    font-size: 12px;
    opacity: 1;
    /* font-family: "Helvetica", sans-serif; */
    /* color: rgb(33, 25, 34);
    font-size: 12px; */
  }
  
  #modal-background {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    /* z-index: 10000; */
  }
  
  #modal-content {
    position: absolute;
    background-color:white;
    
    width: 484px;
    background-color: rgb(255, 255, 255);
    border-radius: 32px;
    position: relative;
    text-align: center;
    margin: auto;
    box-shadow: rgba(0, 0, 0, 0.45) 0px 2px 10px;
    box-sizing: border-box;
    display: block;
    min-height: 400px;
    min-height: initial;
    padding: 20px 10px 24px;
    color: rgb(33, 25, 34);
    font-size: 12px;
  }

  button {
    font-family: Helvetica;
    border-radius: 24px;
    cursor: pointer;
    /* font-size: 300; */
    min-width: 60px;
    min-height: 40px;
    border: 0;
    justify-content: center;
    align-items: center;
    display: flex;
    box-sizing: border-box;
    padding-left: 12px;
    padding-right: 12px;
    padding-top: 8px;
    padding-bottom: 8px;
    /* font-size: 100%; */
    /* font-weight: 500; */
    font-size: 16px;
    font-weight: 500;
  }

  hr.solid {
    border-bottom: 1px solid rgb(222, 222, 222);
    margin: 10px auto;
    width: 110px;  
  }

  .logo {
    height: 45px;
    width: 45px;
    margin: 8px auto 6px;
    box-sizing: border-box;
    display: block;
    text-align: center;
  }

  h1 {
    color: rgb(51,51,51);
    font-size: 32px;
    font-weight: 600;
    letter-spacing: -1.2px;
    padding-left: 16px;
    padding-right: 16px;
    display: block;
    text-align: center;
    /* width: 400px; */
    box-sizing: border-box;
    /* margin-block-start: 0.67em;
    margin-block-end: 0.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px; */
    /* font-family: "Segoe-UI-Bold"; */
  }

label {
  font-size: 14px;
  font-weight: 400;
  text-align: left;
  color: #111;
  display: block;
  margin-left: 8px;
  margin-bottom: 4px;
}

/* To edit below */
input {
  /* font-size: 14px;
  border: 1px solid #ccc;
  padding: 5px 10px; */
  border: #cdcdcd;
  box-sizing: border-box;
  min-height: 48px; 
  color:  #111;
  background-color: #fff;
  width: 12;
  padding: 8px 16px;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 16px;
  white-space: nowrap;
  appearance: none;
  border-radius: 16px;
  border-style: solid;
  border-width: 2px;
  vertical-align: middle;
  margin: 0px;
  margin-bottom: 7px;
  width: 100%
}

.terms {
  -webkit-font-smoothing: antialiased;
  font-size: 11px;
  font-weight: normal;
  text-align: center;
  transition: opacity 0.2s linear 0s;
  color: rgb(118, 118, 118);
}

